import { Component, Inject, Input, OnInit, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { FormBuilder, Validators } from '@angular/forms';
import { MeetingScheduleFacade } from '@feature/meeting-schedule/meeting-schedule.facade';
import { CalenderHeaderComponent } from '@feature/meeting-schedule/components/calender-header/calender-header.component';
import { MeetingResponse, TimeSlots } from '@core/models/meeting-schedule.model';
import { DAYS_INDEX } from '@core/constants/meeting-days.constant';
import { MatCalendarCellCssClasses } from '@angular/material/datepicker';
import { MeetingType } from '@core/enums';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';

@Component({
    selector: 'findqo-view-meeting',
    templateUrl: './view-meeting.component.html',
    styleUrl: './view-meeting.component.scss',
    standalone: false
})
export class ViewMeetingComponent implements OnInit {
	constructor(
		private readonly fb: FormBuilder,
		private readonly meetingFacade: MeetingScheduleFacade,
		@Inject(PLATFORM_ID) protected platforId,
		private readonly router: Router
	) {}

	@Input() userName!: string;
	@Input() meetingTitle!: string;

	minDate: Date;
	maxDate: Date;
	readonly timeZone$ = this.meetingFacade.getTimeZone$();
	readonly isBrowser = isPlatformBrowser(this.platforId);
	readonly headerComponent = CalenderHeaderComponent;
	readonly meetingType = MeetingType;
	readonly appRoute = APP_ROUTE;
	meetingEventData: MeetingResponse;
	timeSlots: TimeSlots[] = [];
	isFormEnabled = false;
	meetingData;
	isFormSubmitted = false;

	form = this.fb.group({
		date: ['', Validators.required],
		timezoneId: ['', Validators.required],
		timeId: ['', Validators.required],
		user: ['', Validators.required],
		title: ['', Validators.required],
		meetingTypeId: ['', Validators.required],
		dayId: ['', Validators.required],
	});

	handleChange(key: string, value: string): void {
		if (key === 'date') {
			const DATE = this.meetingEventData.availability.find((day) => {
				if (DAYS_INDEX[+day.day.id] === new Date(value).getDay()) {
					this.form.patchValue({ dayId: day.day.id });
					return true;
				}
				return false;
			});

			// reset the selected time slot if different date has been selected
			this.form.get('timeId').reset();
			const SELECTED_DATE = new Date(value);

			const DATE_STRING = `${SELECTED_DATE.getFullYear()}-${SELECTED_DATE.getMonth() + 1 > 9 ? SELECTED_DATE.getMonth() + 1 : `0${SELECTED_DATE.getMonth() + 1}`}-${SELECTED_DATE.getDate() > 9 ? SELECTED_DATE.getDate() : `0${SELECTED_DATE.getDate()}`}`;

			this.meetingFacade
				.getTimeSlots$({
					startTime: DATE.time[0].start.reference,
					endTime: DATE.time[0].end.reference,
					date: DATE_STRING,
					meetingId: this.meetingEventData.id,
				})
				.subscribe((slots) => (this.timeSlots = slots));
		}

		this.form.get([key]).patchValue(value);
	}

	myFilter = (d: Date | null): boolean => {
		return this.meetingEventData.availability.some((day) => DAYS_INDEX[+day.day.id] === (d || new Date()).getDay());
	};

	dateClass = (d: Date): MatCalendarCellCssClasses => {
		if (
			this.meetingEventData.availability.some((day) => DAYS_INDEX[+day.day.id] === (d || new Date()).getDay()) &&
			(d || new Date()).getTime() > this.minDate.getTime() - 1 &&
			(d || new Date()).getTime() <= this.maxDate.getTime()
		) {
			return 'highlighted-date';
		}

		return null;
	};

	handleNext(): void {
		this.isFormEnabled = true;
	}

	handleBack(): void {
		if (this.isFormEnabled) {
			this.isFormEnabled = false;
			return;
		}

		this.router.navigate([APP_ROUTE.home]);
	}

	onFormSubmit(meetingData): void {
		const { title, user, ...rest } = meetingData;
		const TIME_SLOT = this.timeSlots?.find((ts) => ts.id === meetingData?.timeId);
		this.meetingData = { ...rest, ...this.meetingEventData, timeSlot: TIME_SLOT };
		this.isFormSubmitted = true;
	}

	ngOnInit(): void {
		if (this.isBrowser) {
			this.meetingFacade.getMeetingEvent({ user: this.userName, title: this.meetingTitle }).subscribe(
				(meeting) => {
					this.meetingEventData = meeting;

					this.minDate = new Date(+this.meetingEventData.startDate * 1000);
					this.maxDate = new Date(+this.meetingEventData.endDate * 1000);

					// check if start date is an past date, then current day will shown as start date.
					if (new Date().getTime() > this.minDate.getTime() - 1) {
						this.minDate = new Date();
						this.minDate.setHours(0, 0, 0, 0);
					}

					this.minDate.setDate(this.minDate.getDate() - 0);

					this.form.patchValue({
						title: this.meetingTitle,
						user: this.userName,
						meetingTypeId: meeting.meetingType.id,
						timezoneId: this.meetingEventData?.timezone?.id,
					});
				},
				() => this.router.navigate([this.appRoute.meetingSchedule.inactive])
			);
		}
	}
}
