import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormControl, Validators } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { MeetingScheduleFacade } from '@feature/meeting-schedule/meeting-schedule.facade';
import { finalize } from 'rxjs';

@Component({
    selector: 'findqo-invitee-meeting-form',
    templateUrl: './invitee-meeting-form.component.html',
    styleUrl: './invitee-meeting-form.component.scss',
    standalone: false
})
export class InviteeMeetingFormComponent {
	constructor(
		private readonly fb: FormBuilder,
		private readonly meetingFacade: MeetingScheduleFacade
	) {}

	@Input() previousData;
	@Output() formSubmission = new EventEmitter();
	@ViewChild('phoneNumber') phoneNumber?: MatInput;

	isAddGuestEnabled = false;
	loading = false;

	form = this.fb.group({
		name: ['', Validators.required],
		phoneCode: ['+353', Validators.required],
		phone: [undefined, [Validators.required, Validators.pattern('^[0-9]*$')]],
		email: ['', [Validators.required, Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
		message: ['', Validators.maxLength(2000)],
		guestEmails: this.fb.array([]),
	});

	get emails() {
		return this.form.get('guestEmails') as FormArray;
	}

	mouseClick(): void {
		setTimeout(() => {
			this.phoneNumber?.focus();
		}, 200);
	}

	handleAddGuest() {
		this.isAddGuestEnabled = true;
	}

	addEmail(email): void {
		this.emails.push(new FormControl(email, Validators.required));
	}

	removeEmail(email: string): void {
		const INDEX = this.emails.controls.findIndex((em) => em.value === email);
		this.emails.removeAt(INDEX);
	}

	handleFormSubmit(): void {
		this.loading = true;
		const GUEST_EMAILS = this.form.get('guestEmails')?.value?.join(',');

		const DATE = new Date(this.previousData.date);

		const DATE_STRING = `${DATE.getFullYear()}-${DATE.getMonth() + 1 > 9 ? DATE.getMonth() + 1 : `0${DATE.getMonth() + 1}`}-${DATE.getDate() > 9 ? DATE.getDate() : `0${DATE.getDate()}`}`;

		const API_DATA = {
			...this.form.value,
			...this.previousData,
			guestEmails: GUEST_EMAILS,
			date: DATE_STRING,
			timeZoneId: this.previousData.timezoneId,
		};

		this.meetingFacade
			.acceptMeeting(API_DATA)
			.pipe(finalize(() => (this.loading = true)))
			.subscribe(() => {
				this.formSubmission.emit({ ...this.form.value, ...this.previousData });
			});
	}
}
