import { Component, Inject } from '@angular/core';
import { MatCalendar } from '@angular/material/datepicker';

@Component({
    selector: 'findqo-calender-header',
    templateUrl: './calender-header.component.html',
    styleUrl: './calender-header.component.scss',
    standalone: false
})
export class CalenderHeaderComponent {
	constructor(@Inject(MatCalendar) private calendar: MatCalendar<Date>) {}

	get periodLabel() {
		return this.calendar.activeDate.toLocaleString('default', { month: 'long', year: 'numeric' });
	}

	previousClicked(): void {
		this.calendar.activeDate = new Date(this.calendar.activeDate.getFullYear(), this.calendar.activeDate.getMonth() - 1, 1);
	}

	nextClicked(): void {
		this.calendar.activeDate = new Date(this.calendar.activeDate.getFullYear(), this.calendar.activeDate.getMonth() + 1, 1);
	}
}
